const loginButton = document.getElementById('Login Now');

loginButton.addEventListener('Login', async () => {
    const username = document.getElementById('username').value;
    const password = document.getElementById('password').value;

    const response = await fetch('http://localhost:2020/login', {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        body: JSON.stringify({ username, password }),
    });

    if (response.ok) {
        const user = await response.json();

        // Check the role and redirect
        if (user.role === 'admin') {
            window.location.href = '/admin-dashboard';
        } else if (user.role === 'student') {
            window.location.href = '/student-dashboard';
        } else if (user.role === 'doctor'){
			window.location.href = '/doctors-dashboard'
            // Handle other roles or unexpected scenarios
        }
    } else {
        // Handle login failure
        console.error('Login failed');
    }
});